}
.navbar-toggler:focus {
  box-shadow: 0 0 0 0rem !important;
}

.navbar-nav li a {
  font-size: 0.9rem;
  font-weight: bold;
}

.carousel-control-next,
.carousel-control-prev {
  width: 10% !important;
}

.navbar-brand img {
  height: 40px;
}

.btn-hero svg {
  transition: all 0.7s;
}

.btn-hero:hover svg {
  margin-left: 10px;
}

.btn-hero:hover {
  background-color: #fff !important;
  color: black !important;
}

.inner-slide {
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(to left, #bdc3c73a, #2c3e50a1);
  top: 0%;
  left: 0;
  padding-top: 10%;
  /* padding-left: 100px; */
}

.banner-sm img {
  height: 600px;
}

.inner-slide h1 {
  width: 600px;
}

.img-errorx img {
  top: 0;
  position: absolute;
  max-height: 420px;
  right: 50%;
  -webkit-transform: translateX(50%);
          transform: translateX(50%);
  margin: 0 auto;
}

.divi {
  width: 30%;
  margin-left: 35%;
  border-bottom: 4px solid #bdc3c7;
  padding-bottom: 10px;
}

.divi-left {
  width: 30%;
  border-bottom: 4px solid #bdc3c7;
}

.backgroundImg {
  text-align: center;
  margin: 0;
  padding: 50px 0;
  width: 100%;
  background: #7474bf; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #348ac7,
    #7474bf
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

#footer {
  position: absolute;
  bottom: 0;
  height: 60px; /* Height of the footer */
}

.msl-vars {
  /* --menu-max-height: 0px!important; */
  --input-width: 100% !important;
}

.col-xs-flexstart {
