  .navbar-brand small {
    font-size: 0.8rem !important;
  }

  .carousel-item img {
    height: 400px !important;
  }

  .inner-slide {
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 50%;
  }

  .banner-sm img {
    height: 400px;
  }

  .inner-slide h1 {
    font-size: 2rem;
    width: auto;
  }

  .col-xs-flexstart {
    justify-content: start !important;
  }

